/**
* CSS file to hold styles that apply across the application
*/

@import url("https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap");

html,
body {
  font-family: "Montserrat", sans-serif;
  height: 100%;
  margin: 0;
  overflow-x: hidden;
  padding: 0;
  width: 100%;
}

div#main {
  display: flex;
  flex-direction: column;
  width: 100vw;
}

section {
  position: relative;
  width: 100vw;
}

section.min-height {
  min-height: 100vh;
}

section.padding {
  padding: 5rem 0;
}

section img.background {
  height: 100%;
  left: 50%;
  object-fit: cover;
  pointer-events: none;
  position: absolute;
  top: 0;
  transform: translate(-50%, 0);
  width: 100vmax;
  z-index: -1;
}

h1 {
  font-family: "Cormorant Garamond", serif;
  font-size: clamp(2rem, 8vw, 5.5rem);
  font-weight: 400;
  line-height: 0.8;
  margin: 0;
}

h2 {
  font-size: clamp(1rem, 4vw, 2rem);
  font-weight: 400;
  margin: 0;
  padding-top: 1rem;
}

h3 {
  font-weight: 400;
  font-size: 1.25rem;
  margin: 0;
}

p {
  font-size: 18px;
  line-height: 1.5;
  margin-bottom: 0;
  font-weight: 300;
}

p.large {
  font-size: 24px;
}

p.small {
  font-size: 15px;
}

hr {
  margin: 2.5rem;
}

li {
  line-height: 1.7;
}

li::marker {
  color: #4e567e;
}

a {
  color: black;
  text-decoration: none;
}

a:hover {
  color: #4e567e;
}

nav a:focus {
  outline: none;
  box-shadow: none;
}

.container {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: 330px 300px;
  margin: 20px auto;
}

/* MOBILE: Força flex ao invés de grid */
@media (max-width: 768px) {
  .container {
    display: flex !important;
    grid-template-columns: none !important;
    grid-gap: 0 !important;
  }
}

.container .box {
  border-radius: 10px;
  border: 1px solid rgb(204, 204, 204);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px 16px;
  text-align: center;
}

img.socialIcon {
  height: 30px;
  width: 30px;
}

.home-text-container {
  max-width: 90vw;
  word-break: break-word;
  overflow-wrap: break-word;
  margin: 5rem 2rem 0 2rem;
  width: 100%;
  z-index: 2;
  position: relative; /* ou remova se não precisar */
}

/* Celular (até 768px) - mais abrangente */
@media (max-width: 768px) {
  /* Reset completo para mobile */
  html, body {
    overflow-x: hidden !important;
    width: 100% !important;
    max-width: 100vw !important;
  }
  
  section {
    width: 100% !important;
    max-width: 100vw !important;
    overflow-x: hidden;
    padding: 0 !important;
  }
  
  h1 {
    font-size: 2.5rem !important;
    line-height: 1.1 !important;
    text-align: center !important;
    padding: 0 1rem !important;
    margin: 1rem 0 !important;
  }
  
  h2 {
    font-size: 1.3rem !important;
    padding: 0.5rem 1rem !important;
    text-align: center !important;
    margin: 0.5rem 0 !important;
  }
  
  .container {
    display: flex !important;
    flex-direction: column !important;
    align-items: stretch !important;
    width: calc(100% - 2rem) !important;
    max-width: calc(100% - 2rem) !important;
    margin: 0 auto !important;
    padding: 1rem !important;
    box-sizing: border-box !important;
    gap: 1rem !important;
  }
  
  .container .box {
    width: 100% !important;
    max-width: 100% !important;
    min-width: 0 !important;
    box-sizing: border-box !important;
    margin: 0 !important;
    padding: 1rem !important;
    overflow: hidden !important;
    word-wrap: break-word !important;
    flex: none !important;
  }
  
  section.padding {
    padding: 2rem 0 !important;
  }
  
  /* Texto responsivo */
  h1, h2, h3, p {
    word-break: break-word !important;
    overflow-wrap: break-word !important;
    hyphens: auto !important;
  }
  
  p {
    font-size: 16px !important;
    line-height: 1.6 !important;
    padding: 0 1rem !important;
  }
  
  img.socialIcon {
    height: 28px !important;
    width: 28px !important;
  }
  
  /* About section específica */
  div[style*="backgroundColor: white"][style*="width: 50%"] {
    width: 90% !important;
    padding: 2rem 1rem !important;
    margin: 1rem auto !important;
    max-width: 90% !important;
  }
  
  /* Skills list específica */
  .skills-list {
    columns: 1 !important;
    margin: 1rem 0 !important;
    padding: 0 1rem !important;
    gap: 0 !important;
  }
  
  .skills-list li {
    margin-bottom: 0.5rem !important;
    break-inside: avoid !important;
    page-break-inside: avoid !important;
  }
  
  /* Portfolio específico */
  div[style*="display: flex"][style*="flexDirection: row"] {
    flex-direction: column !important;
    align-items: center !important;
  }
  
  div[style*="maxWidth: 40%"] {
    max-width: 100% !important;
    width: 100% !important;
    margin-bottom: 1rem !important;
    padding: 0 1rem !important;
  }
}

/* Tablet (769px até 1024px) */
@media (min-width: 769px) and (max-width: 1024px) {
  .container {
    grid-template-columns: 1fr 1fr;
    max-width: 90%;
    margin: 20px auto;
  }
  
  h1 {
    font-size: 3.5rem;
  }
  
  h2 {
    font-size: 1.8rem;
  }
}

/* TV (acima de 1600px) */
@media (min-width: 1600px) {
  h1 {
    font-size: 6rem;
  }
  h2 {
    font-size: 2.2rem;
  }
}

/* EXTREME CASES: Zoom absurdo e janelas microscópicas */
@media (max-width: 320px) {
  /* Ultra pequeno */
  h1 {
    font-size: 1.5rem !important;
    line-height: 1.2 !important;
    padding: 0 0.5rem !important;
    margin: 0.5rem 0 !important;
  }
  
  h2 {
    font-size: 1rem !important;
    padding: 0.25rem 0.5rem !important;
    margin: 0.25rem 0 !important;
  }
  
  .container {
    width: calc(100% - 1rem) !important;
    padding: 0.5rem !important;
    gap: 0.5rem !important;
  }
  
  .container .box {
    padding: 0.5rem !important;
    margin: 0 !important;
  }
  
  p {
    font-size: 14px !important;
    padding: 0 0.5rem !important;
  }
  
  /* About ultra pequeno */
  div[style*="backgroundColor: white"][style*="width: 50%"] {
    width: 95% !important;
    padding: 1rem 0.5rem !important;
    margin: 0.5rem auto !important;
  }
  
  .skills-list {
    margin: 0.5rem 0 !important;
    padding: 0 0.5rem !important;
    font-size: 14px !important;
  }
}

/* Garante que imagens e ícones não ultrapassem o container */
img,
img.socialIcon {
  max-width: 100%;
  height: auto;
  display: block;
}

/* Para evitar sobreposição geral */
* {
  box-sizing: border-box;
  max-width: 100%;
}

/* PROTEÇÃO EXTREMA contra zoom maluco */
html {
  text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

/* Força contenção em qualquer situação */
body, section, div, p, h1, h2, h3 {
  max-width: 100% !important;
  overflow-wrap: break-word !important;
  word-break: break-word !important;
}
